@use 'base/variables' as v;
@use 'base/mixins' as m;

.favorito{
    background-color: v.$blanco;
    border-radius: 1rem;
    padding: 4rem;
    margin-bottom: 2rem;
    box-shadow: 0px 0px 2.6rem -0.8rem darken(v.$grisClaro, 30%);

    transition: scale 300ms;

    &:last-of-type{
        margin-bottom: 0
    }
    @include m.tablet {
        margin-bottom: 0;
        
        &:hover{
            scale: 1.05;
        }
    }

    &__grid{
        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 3fr;
            align-items: center;
            gap: 4rem;
        }
    }

    &__img{
        max-width: 20rem;
        margin: 0 auto 2rem auto;
    }
    &__contenido{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
           align-items: flex-start;
        }
    }

    &__nombre{
        margin: 1rem 0;
        color: v.$primario;
        font-size: 2.4rem;
    }
    &__descripcion{
        margin: 1.5rem 0;
        line-height: 1.5;
        text-align: center;
        font-size: 1.6rem;
        color: v.$secundario;

        @include m.tablet {
            text-align: left;
        }
    }
}