@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonial{
    background-color: v.$secundario;
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 1rem;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include m.tablet {
        margin-bottom: 0;
        transition: scale 300ms;

        &:hover{
            scale: 1.03;
        }

        &:nth-child(1){
            grid-column: 1 / 4;
        }
        &:nth-child(3){
            grid-column: 5 / 6;
            grid-row: 1 / 3;
        }
        &:nth-child(4){
            grid-column: 1 / 3;
        }
        &:nth-child(5){
            grid-column: 3 / 5;
        }
    }

    &__header{
        display: flex;
        gap: 2rem;
        align-items: center;
    }
    &__img{
        flex-basis: 10rem;
        border-radius: 50%;
        overflow: hidden;
    }
    p{
        color: v.$blanco;
    }
    &__autor{
        font-size: 2.5rem;
        font-weight: 700;
        margin: .5rem 0;
    }
    &__texto{
        font-size: 2rem;
        line-height: 1.5;
        margin: 2rem 0 0;
        padding-left: 4rem;
        
        position: relative;

        &::before{
            content: ' " ';
            font-size: 10rem;
            position: absolute;
            left: -.3rem;
            top: -4rem;
            opacity: .3;
            color: v.$blanco;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        }
    }

}